import initCardifyTheme from "./switch";

function appendScriptToPage(tagId, script) {
  appendElementToPage(
    Object.assign(document.createElement("script"), { src: script }),
    tagId,
    "text/javascript"
  );
}

function appendElementToPage(element, tagId, typeT) {
  try {
    document.getElementById(tagId).remove();
  } catch (e) {} //Delete any existing reference
  Object.assign(element, { type: typeT, async: false, tagId: tagId });
  document.getElementsByTagName("head")[0].appendChild(element);
}

appendScriptToPage(
  "HOTKEYJS",
  "https://unpkg.com/hotkeys-js/dist/hotkeys.min.js"
);
appendScriptToPage("tippyjs", "https://unpkg.com/tippy.js@6");

setTimeout(initCardifyTheme, 3000);

if (module.hot) {
  module.hot.dispose(function () {
    console.log("模块即将被替换时");
  });

  module.hot.accept(function () {
    console.log("模块或其依赖项之一刚刚更新时");
  });
}
